@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=IBM+Plex+Mono:400,500,600,700&display=swap);
body, button {
  font-family: 'IBM Plex Mono', monospace; }

.nav-logo img {
  width: 150px;
  max-height: 70px; }

.home-icon {
  color: #FF8578;
  margin: 8%; }

.header {
  background-image: url(/static/media/header_2.26626aa6.gif);
  height: 500px;
  background-repeat: no-repeat;
  background-size: cover; }

.principal {
  background: #FF8578; }

.secondary {
  background: #50D5D9; }

.card-form {
  border-radius: 7px;
  overflow: auto;
  height: 70vh; }

.button {
  border: none; }

.menu .button {
  margin-left: 10px; }

.aside-menu {
  background: #FFAD7A;
  height: 100vh;
  padding: 2%; }

.box {
  padding: 2%; }

.profile-card {
  height: 40vh;
  overflow-y: auto; }

.profile-image {
  width: 200px;
  margin: 0 auto; }

.property {
  color: #38519a;
  font-weight: 600; }

#test {
  padding: 3%; }

.question {
  display: inline-block; }

.multiple-option > input {
  margin-left: 8%;
  margin-right: 10px; }

.Modal {
  position: absolute;
  top: 100px;
  left: 40px;
  right: 40px;
  bottom: 40px;
  outline: none; }

.Overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(51, 51, 51, 0.5); }

.modal-card {
  height: calc(80vh - 40px); }

.new-career {
  margin: 20px; }

.hidden-box {
  display: none; }

.show-box {
  display: block; }

.card-mentor {
  width: 25%; }
  .card-mentor img {
    width: 65%;
    margin: 0 auto; }

.link_1 {
  color: #ffffff; }

.mentor-image {
  margin-right: 20px; }

.bio-mentor {
  font-size: 14px;
  padding: 2%; }

.events-list {
  padding: 20px; }

.menu-item {
  color: #FF8578; }

.nav-button {
  margin-right: 20px; }

